<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>素材管理</span>
        <!-- <el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button> -->
        <el-upload
          style="float: right; padding: 3px 0"
          action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
          :headers="setToken"
          name="image"
          :show-file-list="false"
          :on-success="onSuccess"
        >
          <el-button slot="trigger" size="small" type="primary">上传文件</el-button>
        </el-upload>
      </div>
      <div class="text item">
        <!--el-form搜索表单区域-->
        <ul>
          <li class="image-box" v-for="item in data" :key="item.id">
            <img :src="item.url" alt />
            <div class="image-bot">
              <el-button type="success" icon="el-icon-star-off" circle></el-button>
              <el-button type="danger" icon="el-icon-delete" circle></el-button>
            </div>
          </li>
        </ul>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Material',
  created () {
    // 获取首屏数据
    this.$http.get('/user/images').then(res => {
      this.data = res.data.data.results
    })
    // 获取token 并传递
    this.setToken = this.toke()
    // console.log(this.setToken)
  },
  data () {
    return {
      data: '',
      setToken: ''
    }
  },
  methods: {
    toke () {
      let token = JSON.parse(window.sessionStorage.getItem('userinfo')).token
      return { Authorization: 'Bearer ' + token }
    },
    onSuccess () {
      this.$message.success('图片上传成功！')
      // console.log(this)
      this.$http.get('/user/images').then(res => {
        this.data = res.data.data.results
      })
    }
  }
}
</script>

<style lang="less" scoped>
.image-box {
  list-style: none;
  width: 200px;
  height: 200px;
  background-color: #fff;
  margin: 10px;
  float: left;
  border: 1px solid #eee;
  img {
    width: 200px;
    height: 140px;
  }
  .image-bot {
    width: 200px;
    height: 60px;
    line-height: 60px;
    text-align: center;
  }
}
</style>
